.about {
  padding: 10px 20px 49px 20px;

  @media (min-width: $tablet-width) {
    box-sizing: border-box;
    padding: 0 30px 47px;
    border-bottom: 1px solid $color-grey-600;
  }

  @media (min-width: $desktop-width) {
    padding: 0 110px 26px;
    border-bottom: none;
  }
}

.about__list {
  margin: 0;
  margin-top: 42px;
  padding: 0;
  list-style: none;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 249px);
    grid-template-rows: repeat(2, minmax(280px, 1fr));
    gap: 37px 135px;
    margin-top: 32px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: minmax(280px, 1fr);
    column-gap: 75px;
  }
}

.about__list-item {
  padding-bottom: 24px;

  @media (min-width: $tablet-width) {
    position: relative;
    padding-bottom: 0;
  }
}

.about__list-item-text {
  margin: 0;
  width: 199px;
  line-height: 18px;
  color: $color-grey;

  @media (min-width: $tablet-width) {
    width: 223px;
    padding-top: 151px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    width: 230px;
    padding-top: 151px;
  }
}

.about__list-item-point {
  @media (min-width: $tablet-width) {
    position: absolute;
    top: 0;
    left: 113px;
    z-index: -1;
    margin: 0;
    font-family: inherit;
    font-size: 280px;
    line-height: 280px;
    color: $color-grey-400;
  }
}
